import React, { useEffect, useRef } from "react";

const words = [
  "Dr. Koh's Website is still under development",
  "come back when it's ready!",
];

const WordFlicker = () => {
  const offsetRef = useRef(0);
  const iRef = useRef(0);
  const forwardsRef = useRef(true);
  const skipCountRef = useRef(0);
  const partRef = useRef("");
  const skipDelay = 15;
  const speed = 70;

  useEffect(() => {
    const len = words.length;

    const intervalId = setInterval(() => {
      if (forwardsRef.current) {
        if (offsetRef.current >= words[iRef.current].length) {
          ++skipCountRef.current;
          if (skipCountRef.current === skipDelay) {
            forwardsRef.current = false;
            skipCountRef.current = 0;
          }
        }
      } else {
        if (offsetRef.current === 0) {
          forwardsRef.current = true;
          iRef.current++;
          offsetRef.current = 0;
          if (iRef.current >= len) {
            iRef.current = 0;
          }
        }
      }
      partRef.current = words[iRef.current].substring(0, offsetRef.current);
      if (skipCountRef.current === 0) {
        if (forwardsRef.current) {
          offsetRef.current++;
        } else {
          offsetRef.current--;
        }
      }
      document.querySelector(".word").textContent = partRef.current;
    }, speed);

    // Cleanup the interval when the component unmounts
    return () => clearInterval(intervalId);
  }, []); // Empty dependency array since there are no dependencies

  return <div className="word" style={{ color: "#f9f9fb" }}></div>;
};

export default WordFlicker;
